            @if $column_count != 5 {
                grid-template-columns: repeat($column_count, 1fr);
            } @else if $column_count == 5 {
                grid-template-columns: repeat($column_count - 1, 1fr);
                @include mobile-or-tablet-screen {
                    grid-template-columns: repeat($column_count, 1fr);
                }
            }
        }
    }
    &__table-header {
        grid-template-columns: 1fr 2fr;
        &__pre-appstore {
            grid-template-columns: 11rem 36rem 46rem 13.5rem;
        }
    }
    @for $financial_accounts_count from 0 through 6 {
        @for $synthetic_accounts_count from 0 through 2 {
            .cfd-accounts-compare-modal__table-header-for-synthetic-#{$synthetic_accounts_count}-financial-#{$financial_accounts_count} {
                @if $synthetic_accounts_count > 0 and $financial_accounts_count > 0 {
                    grid-template-columns: 1fr ($synthetic_accounts_count + fr) ($financial_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($synthetic_accounts_count + fr) (
                                $financial_accounts_count + fr
                            ) 1fr;
                    }
                } @else if $synthetic_accounts_count == 0 and $financial_accounts_count == 0 {
                    grid-template-columns: 1fr;
                    &__pre-appstore {
                        grid-template-columns: 1fr 1fr;
                    }
                } @else if $synthetic_accounts_count > 0 and $financial_accounts_count == 0 {
                    &__pre-appstore {
                        grid-template-columns: 2fr 1fr;
                    }
                } @else if $synthetic_accounts_count == 0 {
                    grid-template-columns: 1fr ($financial_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($financial_accounts_count + fr) 1fr;
                    }
                } @else {
                    grid-template-columns: 1fr ($synthetic_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($synthetic_accounts_count + fr) 1fr;
                    }
                }
            }
            .cfd-accounts-compare-modal__table-header-for-synthetic-#{$synthetic_accounts_count} {
                &__pre-appstore {
                    grid-template-columns: 1fr 2fr 1fr;
                }
