    padding-bottom: 1rem;
}

.cfd-password-modal {
    &__warning {
        padding: 2.5rem;
        max-width: 50rem;
        align-self: center;
        &-text {
            border-radius: 0.8rem;
            padding: 0.8rem;
            background-color: var(--status-warning);
        }
    }
    &__message {
        max-width: 32rem;
        margin: auto;
        line-height: 1.43;
    }
    &__radio {
        &.dc-radio-group {
            display: grid;
            grid-gap: 2rem;
            margin-top: 0;
            padding-bottom: 2.4rem;
        }
        &.dc-radio-group__item {
            display: flex;
            flex-direction: row;
        }
    }

    &__create-password {
        padding: 0 1.2rem;

        &-content {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: center;
            margin-top: -2.5rem;

            .dc-icon {
                margin-left: 1.1rem;
                flex: none;
            }
        }
        &-title {
            margin-top: 2.4rem;
            margin-bottom: 0.8rem;
        }
        &-description {
            max-width: 30rem;
            margin-bottom: 1.6rem;
        }
    }
    &__change-password-confirmation {
        display: flex;
        align-items: center;
        flex-direction: column;
        max-width: 30rem;

        @include desktop-screen {
            margin-top: -2.5rem;
        }

        &-wrapper {
            display: flex;
            justify-content: center;
            width: 100%;
        }
        .dc-form-submit-button {
            margin-bottom: 2.2rem;
        }
    }
}

.dc-modal__container_top-up-virtual {
    width: 384px;
    min-height: 367px;
    display: flex;
    flex-direction: column;

    &__body {
        display: flex;
        flex-direction: column;
        padding: 4.8rem 4.2rem;
        flex-grow: 1;
        justify-content: space-around;
        align-items: center;
    }
    &__description {
        text-transform: none;
    }
    &--h4 {
        @include typeface(--small-center-bold-black);
        text-transform: none;
        color: var(--text-prominent);
    }
    &--balance {
        @include typeface(--title-center-bold-black);
        line-height: 1.5;
        color: var(--text-profit-success);
    }
}

.cfd-success-topup {
    &__heading {
        @include typeface(--paragraph-center-bold-black);
        margin: 1.6rem 1.8rem;
    }
    &__description {
        p {
            @include typeface(--small-center-normal-black);
            color: var(--text-general);
            text-transform: none;
        }
    }
}

.cfd-dashboard {
    &__container {
        & .dc-popover__target {
            display: flex;
        }
    }
}

.cfd-verification-email-sent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__title {
        @include typeface(--title-center-bold-black);
        margin: 0.8rem 0;

        &--sub {
            margin-bottom: 0.8rem;
            margin-top: 3rem;
        }
    }
    &__resend-button {
        display: block;
        @include typeface(--paragraph-center-bold-black);
        color: var(--brand-red-coral);
        text-decoration: none;
        margin: 3.2rem 0 0;
    }
    & .countdown {
        margin: 0 0.4rem;
    }
}

.cfd-reset-password {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include typeface(--paragraph-left-normal-black);
    color: var(--text-prominent);

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;
        width: 100%;
        justify-items: center;
        flex-grow: 1;
        gap: 4rem;
        padding: 2.4rem 0;

        @include mobile-or-tablet-screen {
            min-height: 24rem;
            width: calc(100vw - 3.2rem);
        }
    }
    &__heading {
        @include typeface(--paragraph-center-bold-black);
        margin: 1.6rem 0 0;
    }
    &__hint {
        @include typeface(--small-left-normal-black);
        color: var(--text-less-prominent);
        margin-left: 1.2rem;
    }
    &__password-field {
        margin-bottom: 0.5rem;

        & .dc-input__label {
            top: 0.9rem;
        }
    }
    &__password-area {
        @include desktop-screen {
            width: calc(min(34rem, 100vw));
        }
        @include mobile-or-tablet-screen {
            width: 100%;
            max-width: calc(100% - 2.4rem);
        }
    }
    &__error {
        display: flex;
        max-width: 38.4rem;
        min-height: 28.4rem;
        flex-direction: column;
        align-items: center;
        padding: 2rem 0;
        margin: 0 auto;
    }
    &__description {
        @include typeface(--paragraph-left-normal-black);
        color: var(--text-prominent);
        margin-bottom: 2.4rem;

        &--is-centered {
            margin-bottom: 2.4rem;
            max-width: 70%;
        }
    }
    &__confirm-button {
        margin-top: 2.4rem;
    }
    &__success {
        min-width: 38.4rem;
        height: 28.4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding: 2.4rem 0 2rem;

        @include mobile-or-tablet-screen {
            width: calc(100vw - 3.2rem);
            height: 100%;
        }
    }
    @include mobile-or-tablet-screen {
        & .dc-form-submit-button {
            all: unset;
        }
    }
}

/** @define poi-icon-row; weak */
.poi-icon-row {
    display: flex;
    margin-bottom: 16px;

    &__icon-container {
        align-items: center;
        display: flex;
        flex-direction: column;
        color: var(--text-less-prominent);

        &:not(:first-child):not(:last-child) {
            margin: 0 40px;
        }
        p {
            font-size: var(--text-size-xxs);
        }
        p:first-of-type {
            line-height: 1.5;
            margin-top: 8px;
            font-weight: bold;
        }
        @include mobile-or-tablet-screen {
            &:not(:first-child):not(:last-child) {
                margin: 0;
            }
            & .dc-icon {
                width: 8.8rem;
                height: auto;

                /* iPhone SE screen width fixes due to UI space restrictions */
                @media only screen and (max-width: 340px) {
                    width: 7rem;
                }
            }
            p {
                line-height: 20px;
            }
        }
    }
    @include mobile-or-tablet-screen {
        display: grid;
        grid-gap: 2.4rem;
        grid-template-columns: 1fr 1fr 1fr;
        margin-top: 4rem;
    }
}

.dc-modal__container_cfd-financial-stp-modal {
    .dc-modal-header {
        border-bottom: 1px solid var(--general-section-1);
    }
}

.dc-modal__container_jurisdiction-modal {
    overflow-y: scroll;

    .dc-modal-header {
        border-bottom: 2px solid var(--general-section-1);
    }
}

.poi-poa-submitted {
    &__message-content {
        align-items: center;
        display: flex;
        flex-direction: column;
        height: auto;
        justify-content: center;
        padding: 10rem 3rem 7rem;
        line-height: 5rem;
        .dc-btn {
            margin-top: 2rem;
        }
    }
    &__text {
        padding-top: 1.6rem;
    }
    &__svg-footer {
        display: flex;
        margin: 1.6rem 0;
        .dc-btn {
            margin: 0.5rem;
        }
    }
}

.cfd-password-change {
    &-list {
        list-style: disc;
        margin-top: 0.4rem;
        &-container {
            padding-left: 2.5rem;
            margin-bottom: 1.3rem;
            font-size: 1.6rem;
            color: var(--text-general);
        }
    }
    &-modal {
        &-description {
            margin: 1.5rem 0 2rem;
            @include mobile-or-tablet-screen {
                margin: 1rem 1rem 2rem;
                &--button {
                    display: flex;
                    width: 100%;
                }
                &--footer {
                    padding: 1rem 1.6rem;
                    border-top: 1px solid var(--general-hover);
                }
            }
            @include tablet-portrait-screen {
                margin-inline: 0;
            }
        }
    }
    &__content {
        border-top: 1px solid var(--general-hover);
        // to override the default padding
        padding: 0 !important;
        &-center {
            justify-content: center;
        }
        @include mobile-or-tablet-screen {
            border-top: none;
        }
    }
    &__password-success {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include mobile-or-tablet-screen {
            padding: 1.4rem 1.6rem 2.4rem;
        }
    }
    &__icon {
        // to adjust the icon to the center
        margin: 0 0 2rem 2.4rem;
        @include mobile-or-tablet-screen {
            margin: 0 0 2.4rem 2.4rem;
        }
    }
    &__heading {
        margin-bottom: 0.8rem;
    }
    &__subtext {
        margin-bottom: 1.6rem;
    }
    @include desktop-screen {
        &__footer {
            &-button {
                padding: 0 1rem 1rem 0;
                align-self: self-end;
            }
        }
        &__container {
            padding: 0 1.3rem;
            border-bottom: 1px solid var(--general-hover);
        }
    }
    &__footer-button {
        @include mobile-or-tablet-screen {
            padding: 1rem;
        }
        @include tablet-portrait-screen {
            width: 100%;
            margin-block-end: auto;
            .dc-form-submit-button {
                gap: 0.8rem;
                .dc-btn {
                    margin: 0;
                    flex: 1 0 auto;
                }
            }
        }
    }
    &__header {
        .dc-mobile-dialog__title {
            @include tablet-portrait-screen {
                margin-inline: auto;
                font-size: var(--text-size-s);
            }
        }
    }
    &__wrapper {
        &.dc-mobile-dialog__cfd-password-modal {
            @include tablet-portrait-screen {
                max-width: 60rem;
            }
        }
    }
    &__container {
        &.cfd-password-modal__content.dc-modal__container_cfd-password-modal__body {
            @include tablet-portrait-screen {
                height: auto;
                flex-grow: 0;
                margin-block-end: 0;
            }
        }
    }
}
